.cert {
    justify-content: center;
    background-color: #fff;

    &__inner {
        padding: 55px 0;
    }

    &__list {
        margin-top: 55px;
        margin-bottom: 30px;
    }

    &__img {
        width: 100%;
        object-fit: scale-down;
        margin-bottom: 30px;
        transform: scale(1);
        transition: transform .4s,
            box-shadow .4s;

        &:hover {
            transform: scale(1.07);
            box-shadow: 0 0 25px 25px rgba(0, 0, 0, 0.3);
        }
    }

    @media (max-width: 992px) {
        &__inner {
            padding: 40px 0 50px;
        }
    }

    @media (max-width: 768px) {
        &__list {
            margin-top: 0;
            margin-bottom: 0;
        }

        &__img {
            margin: 30px 0;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}